import user from '../user/user'
import paging from '@/components/yang-paging/fy.vue'
export default {
	components: {
		paging,
		user
	},
	data() {
		return {
			userlogin: uni.getStorageSync('token') ? true : false,
			filter: {
				pageNum: 1,
				pageSize: 20
			},
			dataList: {},
			records: []
		}
	},
	onLoad() {
		uni.showLoading()
		this.getFileUrl(this.filter)
	},
	methods: {
		bindLogin() {
			uni.navigateTo({
				url: '../login/login',
			});
		},
		bindLogout() {
			this.logout();
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../login/login',
				});
			}
		},
		dd(index) {
			let filter = {
				pageNum: index,
				pageSize: this.filter.pageSize
			};
			this.filter = filter;
			this.getFileUrl(filter)

		},
		async getFileUrl(filter) {
			try {
				let res = await this.$api.getFileUrl(filter);
				console.log(res)
				if (res.code === 200) {
					this.dataList = res.dataMap;
					this.records = res.dataMap.records;
					uni.hideLoading();
				} else {
					uni.hideLoading();
					uni.showToast({
						title: res.message,
						mask: false,
						icon: 'none',
						duration: 1500
					});
				}
			} catch (e) {
				uni.hideLoading();
				uni.showToast({
					title: '请求出错',
					mask: false,
					icon: 'none',
					duration: 1500
				});
				//TODO handle the exception
			}
		}
	}
}
